import { useEffect, useState } from "react";
import getHello from "../services/hello.service"

const Hello = () => {
return getHello.getHello();
    const[hello, setHello] =  useState([])


    useEffect( () => {
            getHello.getHello()
            .then(
            response => {
                setHello(response.data);
            }
            )
    })

    return hello;
}

export default Hello;